<template>
  <div id="search">
    <div class="search_box">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch"
      >
        <template #action>
          <div @click="onSearch">搜索</div>
        </template>
      </van-search>
    </div>
    <!-- 文本 -->
    <div class="search_text">
      <p>热门搜索</p>
      <ul>
        <li @click="sends('衣服')">衣服</li>
        <li v-for="i in list" :key="i" @click="sends(i)">
          {{ i }}
        </li>
      </ul>
    </div>
    <!-- 分割线 -->
    <van-divider />
    <!-- 搜索结果列表 -->
    <div class="search_list">
      <ul v-if="list">
        <router-link
          tag="li"
          v-for="i in serp"
          :key="i.id"
          :to="`/detail/${i.id}`"
        >
          <Goods :item="i"></Goods>
        </router-link>
      </ul>
    </div>
    <!-- 推荐 -->
    <div class="search_tj" v-if="hotlists.length">
      <!-- 提示没有搜索结果 -->
      <div class="search_ts">
        <img :src="img" alt="" />
      </div>
      <!-- 推荐开始 -->
      <div class="search_sp">
        <SearchNone :arr="hotlists"></SearchNone>
      </div>
    </div>
  </div>
</template>
<script>
// 搜索结果
import Goods from "../components/Index/Goods.vue";
import img from "@/assets/img/2.png";
import SearchNone from "../components/search/SearchNone.vue";
export default {
  components: {
    Goods,
    SearchNone,
  },
  data() {
    return {
      value: "",
      list: [],
      // 搜索结果
      serp: [],
      // 推荐
      hotlists: [],
      img,
    };
  },
  created() {
    // /api/products?page=1&limit=8&keyword=&sid=49&news=0&priceOrder=&salesOrder=
    // 热门搜索接口
    this.axios.get("/api/search/keyword").then((res) => {
      // console.log(res);
      this.list = res.data.data;
    });
  },
  methods: {
    //确认搜索
    onSearch() {
      // 搜索关键字不为空
      if (this.value != "") {
        this.ccoo(this.value);
      }
    },
    // 点击热搜列表
    sends(val) {
      this.value = val;
      this.onSearch();
    },
    // 搜索请求
    ccoo(val) {
      this.axios
        .get("/api/products?page=1&limit=20&keyword=" + val)
        .then((res) => {
          let data = res.data.data;
          // 当data有长度时，表示有搜索结果，否则就进行推荐
          this.serp = data;
          // 没有搜索结果
          if (!data.length) {
            // console.log("没有搜索结果");
            this.referral();
          } else {
            this.hotlists = [];
          }
        });
    },
    // 相关推荐请求
    referral() {
      this.axios.get("/api/product/hot?page=1&limit=20").then((res) => {
        // console.log(res);
        this.hotlists = res.data.data;
        // console.log(this.hotlists);
      });
    },
  },
};
</script>
<style lang="less">
#search {
  width: 100vw;
  padding: 0 0.3rem;
  box-sizing: border-box;
  background-color: #fff;
  // 搜索框
  .search_box {
    .van-search--show-action {
      padding: 0 0;
      // margin-top: 0.3rem;
      padding-top: 0.3rem;
      .van-search__content--square {
        border-radius: 0.5rem;
      }
    }
  }
  //
  .search_text {
    p {
      font-size: 0.28rem;
      color: #999;
      margin: 0.5rem 0.3rem 0.25rem 0.3rem;
      text-align: left;
    }
    ul {
      display: flex;
      li {
        font-size: 0.26rem;
        color: #454545;
        padding: 0 0.21rem;
        height: 0.6rem;
        border-radius: 0.03rem;
        line-height: 0.6rem;
        border: 1px solid #aaa;
        margin: 0 0 0.2rem 0.2rem;
      }
    }
  }
  .search_tj {
    // 图片
    .search_ts {
      width: 7rem;
      height: 6rem;
      margin: 0 auto;
      overflow: hidden;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}
</style>